import React, { useState } from 'react';

import Header from "./header"
import "./layout.css"

function Layout({ children }) {
  let [selection, setSelection] = useState("home");
  return (
    <>
      <Header selected={selection} />
      <div style={{
        maxWidth: `80rem`,
        padding: `3rem 6% 1rem 6%`,
        margin: `0 auto`,
      }}>
        <main>{children}</main>
      </div>
    </>
  )
}

export default Layout
